body {
  background-color: #d3d3d3;
  flex-direction: column;
  align-items: center;
  min-width: 100vw;
  min-height: 100vh;
  padding-top: 20px;
  padding-bottom: 20px;
  animation: 5s linear 2s infinite alternate animatedbackground;
  display: flex;
}

@keyframes animatedbackground {
  0% {
    background-color: #45b4fe;
  }

  25% {
    background-color: #8392ff;
  }

  50% {
    background-color: #e7a2fa;
  }

  100% {
    background-color: #a3f4f8;
  }
}

.form {
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 20px;
  display: flex;
}

.form > #search {
  cursor: pointer;
  letter-spacing: .04em;
  color: #868484;
  border: 1px solid #8080809e;
  border-radius: 6px;
  width: 272px;
  margin-right: 8px;
  padding: 8px 12px;
  font-family: Raleway;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  transition: border-color .25s linear;
}

.form > #search:focus-visible {
  outline: #e59cf9;
  box-shadow: .2px .2px .2px #000000d4, 0 0 20px #0000ffd8, 0 0 3px #00008bd2;
}

.form > button {
  cursor: pointer;
  color: #fff;
  text-shadow: 1px 1px 1px #000, 0 0 25px #00f, 0 0 5px #00008b;
  letter-spacing: .04em;
  background: #4e74ffb6;
  border: 1px solid #8080806e;
  border-radius: 6px;
  width: 91px;
  height: 40px;
  padding: 8px 16px;
  font-family: Raleway;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.5;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.form > button:hover, .form > button:focus {
  background: #6c8cff;
  box-shadow: .2px .2px .2px #000000e8, 0 0 20px #00fe, 0 0 3px #00008bf1;
}

.loader {
  width: 88px;
  height: 88px;
  margin-top: 50px;
  display: none;
  position: fixed;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  border: 4px solid #71d1f7;
  width: 88px;
  height: 88px;
  animation: 2s ease-in-out infinite rotation;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  border-color: #f780e3;
  animation-delay: 1s;
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.images-section {
  justify-content: center;
  width: 100%;
  padding-top: 0;
  padding-bottom: 0;
  display: flex;
}

.gallery-grid {
  grid-template-columns: repeat(5, calc(20% - 20px));
  grid-template-rows: repeat(calc((100% - 130px) / 3));
  place-content: center;
  gap: 15px 20px;
  width: 100%;
  margin-top: 0;
  display: grid;
}

.gallery-card {
  cursor: pointer;
}

a > img {
  object-fit: cover;
  border-radius: 5px;
  width: 100%;
  height: 200px;
}

.img-details-box {
  text-align: center;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  padding-top: 10px;
  display: flex;
}

.detail-item {
