  line-height: 18px;
}

.load-more-btn {
  cursor: pointer;
  color: #fff;
  text-shadow: 1px 1px 1px #000, 0 0 25px #00f, 0 0 5px #00008b;
  letter-spacing: .04em;
  background: #4e74ffb4;
  border: none;
  border-radius: 6px;
  width: 125px;
  height: 40px;
  margin-top: 10px;
  margin-bottom: 10px;
