  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.load-more-btn:hover, .load-more-btn:focus {
  background: #6c8cff;
  box-shadow: .2px .2px .2px #000000e2, 0 0 20px #0000ffe4, 0 0 3px #00008be2;
}

.hidden {
  display: none;
}

:root {
  color: #242424;
